<script>

import ControlElementContainer from '../../controls/Container.svelte';
import ButtonCancel from '../../controls/ButtonCancel.svelte';
import Button from '../../controls/Button.svelte';
import Range from '../../controls/Range.svelte';
import Select from '../../controls/Select.svelte';
import Spinner from '../../controls/Spinner.svelte';

import { pca_fit } from './wasm/pcvpca.js';
import PCV from './wasm/pcvpca_wasm.js';

export let dataset = null;
export let model = null;
export let calres = null;

let promise;
let status = "neutral";
let errMsg = null;
let ncomp;
let center = "yes";
let scale = "no"

async function computeModel(data, ncomp, center, scale) {
   promise = PCV()
      .then(Module => {
         [model, calres] = pca_fit(Module, data, ncomp, center, scale);
         status = "success";
      })
      .catch(e =>{
         status = "error";
         errMsg = e;
      });
}

function compute(e) {
   computeModel(dataset.values, ncomp, center === "yes", scale === "yes");
}

function reset() {
   status = "neutral";
   promise = null;
   model = null;
   errMsg = null;
}

// if data file is empty reset status and error messages
$: if (dataset === null) reset();
$: maxNComp = dataset ? Math.min(dataset.values.nrows - 2, dataset.values.ncols - 1) : null;
$: ncomp = maxNComp ? Math.min(maxNComp, 20) : null;
</script>

{#if promise}

{#await promise}

<ControlElementContainer>
   <Spinner />
   <p>Computing global model...</p>
</ControlElementContainer>

{:then value}
   <!-- status if succeed -->
   {#if model !== null}
   <ControlElementContainer>
      <p>Global model is computed: {model.ncomp} PCs</p>
      <ButtonCancel on:click={reset} />
   </ControlElementContainer>
   {/if}

{:catch error}
   <!-- error message if failed -->
   <ControlElementContainer status="error">
      <p>{error}</p>
   </ControlElementContainer>
   {/await}

{:else}
   <!-- settings for global model calculation -->
   {#if dataset && !model}
   <ControlElementContainer label="# of PCs">
      <Range id="ncomp" bind:value={ncomp} min={1} max={maxNComp} step={1} decNum={0} />
   </ControlElementContainer>
   <ControlElementContainer label="Center">
      <Select id="center" bind:value={center} options={["yes", "no"]} />
   </ControlElementContainer>
   <ControlElementContainer label="Scale">
      <Select id="scale" bind:value={scale} options={["yes", "no"]} />
   </ControlElementContainer>
   <ControlElementContainer label=" ">
      <Button on:click={compute}>Create model</Button>
   </ControlElementContainer>
   {/if}

{/if}

<!-- error message -->
{#if status === "error" || errMsg}
<ControlElementContainer status="error">
   <p>{errMsg}</p>
</ControlElementContainer>
{/if}
