<script>
import Select from '../../controls/Select.svelte';
import ControlElementContainer from '../../controls/Container.svelte';
import ButtonCancel from '../../controls/ButtonCancel.svelte';
import Button from '../../controls/Button.svelte';
import Spinner from '../../controls/Spinner.svelte';

import { pcvpca, pca_predict } from './wasm/pcvpca.js';
import PCV from './wasm/pcvpca_wasm.js';

export let dataset = null;
export let model = null;
export let pvset = null
export let pvres = null

let nseg = 4;
let cvtypeOptions = ['ven', 'rand', 'loo'];
let cvType = 'ven';
let status = 'neutral';
let errMsg = null;
let promise;

$: if (dataset && dataset.values && dataset.values.nrows < 4) {
   cvtypeOptions = ['loo'];
   cvType = 'loo';
}

function downloadPVSet(e) {
   const blob = new Blob([pvset.toCSV(dataset.sep, dataset.dec, dataset.header, dataset.labels)], { type: 'text/csv' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', 'pvset.csv');
   a.click()
}

async function computePVSet(data, model, cv) {
   promise = PCV()
      .then(Module => {
         const cv_type = cv.type == 'loo' ? 0 : cv.type == 'rand' ? 2 : 1;
         pvset = pcvpca(Module, model, data, model.ncomp, cv.nseg, cv_type, 0);
         pvres = pca_predict(Module, model, pvset);
      })
      .catch((e) => {
         status = 'error';
         errMsg = e;
      });
}

function compute(e) {
   computePVSet(dataset.values, model, {type: cvType, nseg: nseg});
}

function reset() {
   status = 'neutral';
   errMsg = null;
   promise = null;
   pvset = null;
}

// if data file is empty reset status and error messages
$: if (model === null) reset();

// adjust options for number of CV segments based on dataset size
$: nsegOptions = dataset && dataset.values.nrows > 0 ?
   [2, 3, 4, 5, 10, 20].filter(v => v <= dataset.values.nrows / 2 ) : [];

</script>


{#if (model && !pvset) && status !== 'busy'}
<ControlElementContainer label='Split type:'>
   <Select id='cvType' bind:value={cvType} options={cvtypeOptions} />
</ControlElementContainer>

{#if cvType !== 'loo'}
<ControlElementContainer label='Segments:'>
   <Select id='nseg' bind:value={nseg} options={nsegOptions} />
</ControlElementContainer>
{/if}

<ControlElementContainer label=' '>
   <Button on:click={compute}>Compute PV-set</Button>
</ControlElementContainer>
{/if}

{#if promise}

{#await promise}
   <ControlElementContainer>
      <Spinner />
      <p>Computing PV-set...</p>
   </ControlElementContainer>
{:then value}
   {#if pvset !== null}
   <ControlElementContainer>
      <p>PV-set is computed: {pvset.nrows} &times; {pvset.ncols}</p>
      <ButtonCancel on:click={reset} />
   </ControlElementContainer>
   <ControlElementContainer label=' '>
      <Button on:click={downloadPVSet}>Download</Button>
   </ControlElementContainer>
   {/if}

{:catch error}
   <ControlElementContainer status='error'>
      <p>{error}</p>
   </ControlElementContainer>
{/await}

{/if}


{#if status === 'error' && errMsg}
   <ControlElementContainer status='error'>
      <p>{errMsg}</p>
   </ControlElementContainer>
{/if}

