<script>
   /*************************************************************/
   /* Svelte component for showing plots                        */
   /*************************************************************/

   // import plot and plot controls components
   import DistancePlot from './plots/DistancePlot.svelte';
   import DistancePlotControls from './plots/DistancePlotControls.svelte';

   import ExtremePlot from './plots/ExtremePlot.svelte';
   import ExtremePlotControls from './plots/ExtremePlotControls.svelte';

   import ScoresPlot from './plots/ScoresPlot.svelte';
   import ScoresPlotControls from './plots/ScoresPlotControls.svelte';

   import VariancePlot from './plots/VariancePlot.svelte';
   import VariancePlotControls from './plots/VariancePlotControls.svelte';

   /*****************************************/
   /* Input parameters                      */
   /*****************************************/

   export let model;
   export let res;

   // selected plot number and its parameters
   let selected_plot = 0;
   let params = null;

   const options = [
      {label: 'Variance', plotComponent: VariancePlot, controlsComponent: VariancePlotControls},
      {label: 'Scores', plotComponent: ScoresPlot, controlsComponent: ScoresPlotControls},
      {label: 'Extremes', plotComponent: ExtremePlot, controlsComponent: ExtremePlotControls},
      {label: 'Distance', plotComponent: DistancePlot, controlsComponent: DistancePlotControls}
   ]

   function switchPlot(i) {
      params = null;
      selected_plot = i;
   }
</script>

<div class="plot-container">
   <!-- column with current plot -->
   <div class="plot-area">
      <svelte:component this={options[selected_plot].plotComponent} {model} {res} {params}  />
   </div>
   <!-- column with plot selectors and current plot settings -->
   <div class="plot-controls-area">
      {#each options as {label, plotComponent, controlsComponent}, i}
      {#if selected_plot === i}
      <svelte:component this={controlsComponent} bind:params={params} {model} {res} />
      {:else}
      <button class="plot-selector" on:keydown={() => switchPlot(i)} on:click={() => switchPlot(i)}>{label}</button>
      {/if}
      {/each}
   </div>
</div>


<style>
   .plot-container {
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      padding: 0;
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: 1fr minmax(270px, 30%);
   }

   .plot-area {
      padding: 1em;
      margin-top: 0.25rem;
      border-radius: 0.25em;
      background: #fefefe;
   }

   .plot-controls-area {
      padding: 0 0.25em 0 0.5em;
   }

   .plot-selector {
      cursor: pointer;
      color: #606060;
      background: #fffef480;
      box-sizing: border-box;
      padding: 0.5em 1em;
      margin: 0.25em 0;
      border-radius: 0.25rem;
      box-shadow: none;
      border: none;
      width: 100%;
      text-align: left;
      font-size: 1rem;
   }

   .plot-selector:hover {
      background: #e0e0e0;
      color: #403020;
   }

   @media(min-width:1100px) {
      .plot-container {
         grid-template-columns: 1fr minmax(285px, 30%);
      }
   }

   @media(min-width:1300px) {
      .plot-container {
         grid-template-columns: 1fr minmax(300px, 30%);
      }
   }

</style>